var BaseView = require("BaseView")

cc.Class({
    extends: BaseView,

    properties: {
        
    },

    onLoad: function () {
        this._super()
    },

    setData: function (data) {
        this.data = data
        this.find("txtTitle").string = data.title
        cc.resMgr.loadNetUrl(this.find("imgContent"), data.content)
    },

    show: function () {
        this._super()
    },

    hide: function () {
        this._super()
    },

});
